body {
  font-family: "Roboto";
  line-height: 1.3rem;
}

.layout {
  display: grid;
  grid-template-rows: auto auto 1fr auto;
  min-height: 100vh;
  background-color: #fff;
}
@media (min-width: 768px) {
  .layout {
    max-width: 1440px;
    margin: 0 auto;
  }
}

.display {
  display: flex;
  flex-direction: column;
  margin: 0 auto;
  max-width: 100%;
  flex: 1;
}
.display-content {
  flex: 20;
  padding: 0 0.5rem;
  margin-bottom: 2rem;
}
.display-sidebar {
  display: flex;
  flex-direction: column;
}
.display-sidebar-start {
  order: 1;
  width: 100%;
  background: transparent;
}
.display-sidebar-end {
  order: 2;
  width: 100%;
  background: transparent;
}
.display-sidebar-left {
  order: 1;
  width: 100%;
  background: transparent;
}
.display-sidebar-right {
  order: 2;
  flex: 1 1 auto;
  padding: 0 1rem;
}
@media (min-width: 1024px) {
  .display {
    width: 100%;
    max-width: 100%;
    margin: 0 auto;
    flex-direction: row;
    justify-content: center;
  }
  .display-content {
    padding: 0 2rem 2rem 2rem;
  }
  .display-sidebar {
    flex-direction: row;
  }
  .display-sidebar-start {
    order: 0;
    width: 25%;
  }
  .display-sidebar-end {
    width: 25%;
  }
  .display-sidebar-left {
    order: 0;
    width: 31.25%;
  }
  .display-sidebar-right {
    flex: 1 0 25%;
    padding: 8rem 1rem 1rem 0;
  }
}
@media (min-width: 1260px) {
  .display-sidebar-right {
    flex: 1 0 25%;
  }
}

.content {
  flex: 1;
}

.article {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 0;
  padding: 0;
  flex: 1;
}
.article-header {
  display: flex;
  color: #333;
  align-self: stretch;
  padding: 0 0 0.25rem 0;
  margin: 0 1rem;
  text-wrap: normal;
}
.article-header h1 {
  font-size: 1.5rem;
  line-height: 1.5rem;
  color: #333;
  margin: 0 0.25rem;
  margin-left: 0.5rem;
  padding: 1.5rem 0 0;
}
.article-header h2 {
  font-size: 0.95rem;
  padding: 0.25rem;
  margin: 0 0 0 0.5rem;
}
.article-header a {
  color: #66e;
}
.article-header a:hover {
  color: #026F6F;
}
.article-header address {
  font-size: 0.8rem;
  font-weight: 400;
  padding-right: 1rem;
  margin-left: auto;
  padding-top: 0.5rem;
}
.article-footer {
  display: flex;
  justify-content: center;
  align-items: center;
  align-self: center;
  width: 100%;
  max-width: 60rem;
  padding: 1rem 0;
  color: #fff;
}
@media (min-width: 768px) {
  .article {
    max-width: 100%;
    margin: 0 auto;
  }
  .article header {
    max-width: 60rem;
  }
  .article header h1 {
    font-weight: bold;
    font-size: 2rem;
    line-height: 2.2rem;
    margin-bottom: 0.75rem;
    margin-left: 0.5rem;
    margin-right: -2rem;
  }
  .article header h2 {
    font-size: 1.1rem;
    margin-left: 0.75rem;
  }
}
@media (min-width: 1024px) {
  .article-header {
    margin: 0;
  }
}

.image-right {
  float: right;
  padding: 0 0 1rem 1rem;
  clear: both;
  max-width: 50%;
  height: auto;
}
.image-left {
  float: left;
  padding: 0 1rem 1rem 0;
  max-width: 50%;
  height: auto;
}

.argument_box {
  margin: 1rem 0;
  padding-bottom: 1em;
}
.argument_box div {
  display: flex;
  padding: 0;
}
.argument_box div p:nth-of-type(odd) {
  display: flex;
  justify-content: flex-end;
  align-items: center;
  flex: 1;
  font-weight: bold;
  padding: 0.5em 1em;
}
.argument_box div p:nth-of-type(even) {
  display: flex;
  justify-content: flex-start;
  align-items: flex-start;
  font-weight: normal;
  padding: 0.5em 1em 0.5em 0;
  flex: 9;
}

.navbar {
  color: #fff;
  background: linear-gradient(#55595C, #000407);
  border-top: 2px solid #55595C;
  border-bottom: 2px solid #000407;
}
.navbar-menu {
  display: flex;
  align-items: center;
}
.navbar-hamburger {
  position: relative;
  flex: 1;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  height: 2.25rem;
  margin-left: 0.5rem;
}
@media (min-width: 768px) {
  .navbar {
    justify-content: flex-start;
  }
  .navbar-hamburger {
    display: none;
  }
}

.menu-small {
  flex: 1;
  position: absolute;
  left: 0;
  right: 0;
  top: 100%;
}
.menu-small ul {
  display: flex;
  flex-direction: column;
  width: 100%;
  list-style: none;
  margin: 0;
  padding: 0;
}
.menu-small ul li {
  display: flex;
  width: 100%;
  flex-direction: column;
  background: #333;
  border-bottom: 1px solid #999;
}
.menu-small ul li:hover > ul {
  display: flex;
}
.menu-small ul li a {
  padding: 1rem;
  color: #eee;
  text-decoration: none;
}
.menu-small ul li a[href]:hover {
  color: #111;
  background: linear-gradient(to bottom, #9FD9D9, #397373);
}
.menu-small ul li a:not([href]):after {
  content: var(--content, "[+]");
  margin: 0 0 0 0.6rem;
}
.menu-small ul li a:not([href]):after:hover {
  cursor: default;
}
.menu-small ul ul {
  display: none;
  background: #555;
}
.menu-small ul ul li {
  background: #444;
}
.menu-small ul ul li a:not([href]) {
  background: #555;
}
.menu-small ul ul ul > li {
  background: #777;
}
.menu-small-h-minus {
  --content: "[−]";
}
@media (min-width: 768px) {
  .menu-small {
    display: none;
  }
}
.menu-large {
  display: none;
  flex-direction: row;
  flex: 1;
  max-width: 100%;
  margin: 0 auto 0 2rem;
}
.menu-large ul {
  padding: 0;
  margin: 0 0 0 0rem;
}
.menu-large ul li {
  display: inline-flex;
  flex-direction: column;
  position: relative;
  justify-content: center;
  text-align: center;
  min-width: 4rem;
  margin: 0;
}
.menu-large ul li a {
  color: #fff;
  padding: 0.6rem 1.5rem;
  text-decoration: none;
}
.menu-large ul li a:not([href]):after:not(:first-of-type) {
  content: "▼";
  margin: 0 0 0 0.6rem;
}
.menu-large ul li a:not([href]):after:not(:first-of-type):hover {
  cursor: crosshair;
}
.menu-large ul li:hover, .menu-large ul li:hover a {
  background: linear-gradient(to bottom, #f6f6f6, #888);
  color: #000;
}
.menu-large ul li:hover > ul, .menu-large ul li:hover a > ul {
  left: 0;
}
.menu-large ul ul {
  position: absolute;
  left: -9999px;
  top: 100%;
  width: max-content;
  min-width: 165px;
  padding: 0;
}
.menu-large ul ul li {
  display: flex;
  margin: 0;
  text-align: left;
  border-bottom: 1px solid #999;
}
.menu-large ul ul li:hover > ul {
  left: 100%;
  top: 0;
}
.menu-large ul ul a {
  color: #fff;
  padding: 0.8rem 1.5rem;
  border-bottom: 1px solid #666;
}
.menu-large ul ul a[href] {
  background: #ccc;
}
.menu-large ul ul a[href]:hover {
  background: #026F6F;
  color: #fff;
}
.menu-large ul ul a:not([href]) {
  background: #ccc;
  cursor: not-allowed;
}
.menu-large ul ul a:not([href]):hover {
  background: #aaa;
}
.menu-large ul ul a:not([href]):after {
  content: "►";
  margin: 0 0 0 0.75rem;
  float: right;
}
.menu-large ul ul ul {
  border: 0;
}
.menu-large ul ul ul li:first-child {
  border-left: 1px solid #242525;
}
.menu-large ul ul ul li:not(:first-child) {
  border-left: 1px solid #666;
}
@media (min-width: 1024px) {
  .menu-large {
    display: flex;
  }
}
@media (min-width: 768px) {
  .menu-large {
    display: flex;
  }
}

.hamburger {
  margin-left: 0.25rem;
  margin-right: 0.5rem;
  font-size: 1.6rem;
}
.hamburger:hover {
  cursor: pointer;
}
@media (min-width: 768px) {
  .hamburger {
    display: none;
  }
}

.form-container {
  background: linear-gradient(#f6f6f6, #f0f0f0);
  padding: 2rem 2rem 1rem;
  border: 1px solid #666;
  border-radius: 0.5rem;
  margin: 0 1rem;
}
.form-row {
  display: flex;
  flex-direction: column;
  margin-bottom: 1rem;
}
.form-row label {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  flex: 1;
  font-weight: bold;
  padding: 0.2rem 0;
}
.form-row input:not([type=checkbox]) {
  flex: 1;
  padding: 0.5rem 0.5rem;
  border: 1px solid #666;
  font-size: 1rem;
}
.form-row input:not([type=checkbox]):focus {
  box-shadow: 0px 0px 2px 2px #026F6F;
}
.form-row button {
  margin-left: auto;
}
.form-last-label {
  border-bottom: 1px solid #666;
}
.form-error {
  font-size: 0.8rem;
  color: red;
  margin-left: auto;
  padding-right: 1rem;
}
.form-error ~ input {
  border: 1px solid red;
}
@media (min-width: 768px) {
  .form-container {
    padding: 3rem 3rem 0;
  }
  .form-row {
    flex-direction: row;
    margin-bottom: 1.75rem;
  }
  .form-row label {
    position: relative;
    flex: 0 0 10rem;
    justify-content: flex-end;
    padding-right: 1rem;
  }
  .form-last-label {
    border-bottom: 0;
    border-right: 1px solid #666;
  }
  .form-error {
    position: absolute;
    top: -1rem;
    left: 100%;
    width: 20rem;
    font-size: 0.8rem;
    color: red;
    margin-left: auto;
    padding-right: 1rem;
  }
  .form-error ~ input {
    border: 1px solid red;
  }
}

.form-switch {
  position: relative;
  display: inline-block;
  width: 60px;
  height: 34px;
}
.form-switch input[type=checkbox] {
  opacity: 0;
  width: 0;
  height: 0;
}
.form-switch input[type=checkbox]:checked + .form-switch input[type=checkbox] form-switch-slider {
  background-color: #2196F3;
}
.form-switch input[type=checkbox]:checked + .form-switch input[type=checkbox] form-switch-slider:before {
  -webkit-transform: translateX(26px);
  -ms-transform: translateX(26px);
  transform: translateX(26px);
}
.form-switch input[type=checkbox]:focus + .form-switch input[type=checkbox] form-switch-slider {
  box-shadow: 0 0 1px #2196F3;
}
.form-switch-slider {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #ccc;
  -webkit-transition: 0.4s;
  transition: 0.4s;
  border-radius: 34px;
}
.form-switch-slider:before {
  position: absolute;
  content: "";
  height: 26px;
  width: 26px;
  left: 4px;
  bottom: 4px;
  background-color: white;
  -webkit-transition: 0.4s;
  transition: 0.4s;
  border-radius: 50%;
}

.blog {
  display: flex;
  flex-direction: column;
}
.blog-item {
  display: flex;
  flex-direction: column;
  margin: 1rem;
  border: 1px solid #999999;
  padding: 0;
  width: 92%;
  box-sizing: border-box;
}
.blog-item-link {
  color: #333;
}
.blog-item-header {
  display: flex;
  flex-direction: column;
  padding: 1rem 1rem 0.25rem 0rem;
  background: linear-gradient(to right, #ddd, #eee 75%, #ddd);
  border-bottom: 1px solid #ccc;
}
.blog-item-header-title {
  display: flex;
  align-items: center;
  flex: 1;
}
.blog-item-header-title h1 {
  flex: 1;
  padding: 0;
  margin: 0;
  text-shadow: 1px 1px #fff;
  margin-bottom: 0.75rem;
  font-size: 1.6rem;
  font-weight: bold;
  line-height: 1.75rem;
  padding: 0 1rem;
}
.blog-item-header-subtitle {
  display: flex;
  align-items: center;
  flex: 1;
  padding: 0;
  margin: 0 0 0 auto;
  font-size: 0.85rem;
}
.blog-item-body {
  display: flex;
  height: 12rem;
  background: #ffffff;
}
.blog-item-body-image {
  flex: 0 0 12rem;
  max-width: 5.3333333333rem;
  height: 100%;
}
.blog-item-body-image img {
  object-fit: cover;
  object-position: center;
  /* Center the image within the element */
  width: 100%;
  height: 100%;
}
.blog-item-body-summary {
  display: flex;
  flex-direction: column;
  flex: 1;
  padding: 0.5rem 1rem;
  font-size: 1.05rem;
  line-height: 1.5rem;
  overflow: auto;
}
.blog-item-body-summary p {
  margin: 0;
}
.blog-item-body-summary img {
  display: none;
}
.blog-item-footer {
  flex: 1;
  align-items: center;
  padding: 0.5rem 1rem;
}
@media (min-width: 1025px) {
  .blog {
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: flex-start;
  }
  .blog-item {
    margin: 0 0 2rem;
    flex: 1 0 auto;
    max-width: 48%;
  }
  .blog-item--first {
    max-width: 100%;
  }
  .blog-item:nth-of-type(odd) {
    margin-left: auto;
  }
  .blog-item-body-image {
    max-width: 12rem;
  }
}

.prose {
  font-size: 1.15rem;
  line-height: 1.5rem;
  padding: 0rem 2rem 1rem;
  color: #333;
  /*  NOTE: This is used for the argument box display*/
}
.prose h3 {
  font-size: 1.6rem;
  margin: 1.5rem 0;
}
.prose figure {
  grid-column: 1/-1;
  margin: 1rem 0;
}
.prose ol, .prose ul {
  margin: 2rem 0;
}
.prose li {
  margin-bottom: 0rem;
}
.prose a {
  border-bottom: 2px dashed #397373;
}
.prose a:hover {
  color: #333;
  background: #ddd;
}
.prose blockquote {
  border-left: 6px solid #888;
  padding: 1px 1.5rem;
}
.prose blockquote p {
  color: #666;
}
.prose footer cite {
  font-size: 1rem;
  line-height: 1.1rem;
}
.prose footer a {
  border-bottom: none;
  color: #1779ba;
}
.prose footer a:hover {
  color: #333;
  background: #ddd;
}
.prose aside {
  display: block;
  float: right;
  width: 9rem;
  background: #eee;
  padding: 0.75rem;
  margin: 0 -2rem 1rem 1rem;
  color: #666;
  font-size: 0.9rem;
  line-height: 1.1rem;
  font-style: italic;
}
.prose img {
  float: right;
  padding: 0 0 1rem 1rem;
  max-width: 40%;
  height: auto;
