}
.responsive_table caption {
  width: 100%;
  padding: 0.5em;
  font-weight: bold;
  font-size: 1.3em;
}
.responsive_table thead tr {
  color: #333333;
  border-bottom: 4px double #666;
  font-size: 1.2em;
}
.responsive_table thead th {
  padding: 0.65rem 1rem;
  white-space: nowrap;
  width: 10rem;
}
.responsive_table thead th:first-of-type {
  width: auto;
}
.responsive_table thead th:nth-last-of-type(6) {
  width: 9rem;
}
.responsive_table thead th:nth-last-of-type(5) {
  width: 11rem;
}
.responsive_table thead th:nth-last-of-type(4) {
  width: 6rem;
}
.responsive_table thead th:nth-last-of-type(3) {
  width: 11rem;
  white-space: nowrap;
}
.responsive_table thead th:nth-last-of-type(2) {
  width: 3rem;
}
.responsive_table thead th:nth-last-of-type(1) {
  width: 3rem;
}
.responsive_table tbody tr {
  background: transparent;
}
.responsive_table tbody tr:hover {
  background-color: #f3f3f3;
}
.responsive_table tbody tr.responsive_table--no-hover:hover {
  background-color: transparent;
}
.responsive_table tbody td:not(.responsive_table-no_results) {
  font-size: 0.9rem;
  padding: 0.5rem 1rem;
  border-bottom: 1px dashed #ccc;
  border-right: 1px dotted #ccc;
}
.responsive_table tbody td:not(.responsive_table-no_results):last-of-type {
  border-right: none;
}
.responsive_table tbody td:not(.responsive_table-no_results):last-of-type:hover {
  background-color: transparent;
}
.responsive_table tfoot td {
  padding: 0.5em;
}
@media (max-width: 768px) {
  .responsive_table {
    border: none;
    display: block;
    text-align: left;
    white-space: normal;
  }
  .responsive_table tbody, .responsive_table tfoot, .responsive_table tr, .responsive_table th, .responsive_table td {
    display: block;
    text-align: left !important;
    white-space: normal;
    padding: 0.5em;
  }
  .responsive_table thead tr {
    position: absolute;
    top: -9999px;
    left: -9999px;
  }
  .responsive_table tbody tr {
    border-bottom: none;
  }
  .responsive_table tbody td:not(.responsive_table-no_results) {
    /* Behave  like a "row" */
    border: none;
    position: relative;
    padding-left: 8.5rem;
    text-align: left;
  }
  .responsive_table tbody td:not(.responsive_table-no_results):last-child {
    border: 0;
  }
  .responsive_table tbody td:not(.responsive_table-no_results):before {
    /* Now like a table header */
    position: absolute;
    content: attr(data-label);
    /* Top/left values mimic padding */
    top: 0.5em;
    left: 0.5em;
    width: 7.5em;
    border-right: 2px inset #666;
    white-space: nowrap;
    font-weight: bold;
  }
}
.responsive_table-clickable:hover {
  background: lightblue;
  cursor: pointer;
}
.responsive_table-center {
  text-align: center;
}
@media (max-width: 1000px) {
  .responsive_table-center {
    text-align: left;
  }
}
