.form-switch {
    position: relative;
    display: inline-block;
    width: 60px;
    height: 34px;

    & input[type=checkbox] {
        opacity: 0;
        width: 0;
        height: 0;

        &:checked + & form-switch-slider {
            background-color: #2196F3;

            &:before {
                -webkit-transform: translateX(26px);
                -ms-transform: translateX(26px);
                transform: translateX(26px);
            }
        }

        &:focus + & form-switch-slider {
            box-shadow: 0 0 1px #2196F3;
        }
    }

    &-slider {
        position: absolute;
        cursor: pointer;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        background-color: #ccc;
        -webkit-transition: .4s;
        transition: .4s;
        border-radius: 34px;


        &:before {
            position: absolute;
            content: "";
            height: 26px;
            width: 26px;
            left: 4px;
            bottom: 4px;
            background-color: white;
            -webkit-transition: .4s;
            transition: .4s;
            border-radius: 50%;
        }
    }
}